import React, { Component, Fragment } from 'react';
import firebase from '../../../../services/firebase';

class ClassListing extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection('labels');
    this.unsubscribe = null;
    this.state = {
      labels: []
    };
  }

  onCollectionUpdate = (querySnapshot) => {
    const labels = [];
    querySnapshot.forEach((doc) => {
      // console.log(doc.data());
      const { latin_name } = doc.data();
      labels.push({
        key: doc.id,
        latin_name,
      });
    });
    this.setState({
      labels
    });
  }

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    const { labels } = this.state;
    console.log(labels);
    return (
      <Fragment>

        <div className="section-header">
          <h1>Manage Classes/Labels</h1>
        </div>
        <div className="section-body">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4>Manage Data</h4>
                  <div className="card-header-action">
                    <a href="" className="btn btn-primary" style={{ borderRadius: '0px !important', paddingTop: '5px', paddingBottom: '5px' }}><i className="fa fas fa-plus"></i> Add New Data</a>
                  </div>
                </div>
                <div className="card-body">
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Nama Latin</th>
                      </tr>
                    </thead>
                    <tbody>
                      {labels.map(label =>
                        <tr id={label.key}>
                          <td>{label.key}</td>
                          <td>{label.latin_name}</td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default ClassListing;